.gif-grid{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(var(--gif-size),1fr));
    grid-template-rows: repeat(auto-fill,var(--gif-size));
}
.gif-grid img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
}